import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/Layout"
import Address from "../assets/svg/address.svg"
import Phone from "../assets/svg/phone.svg"
import Email from "../assets/svg/email.svg"

const Contact = ({data}) =>{

  const contactInfo = data.site.siteMetadata.contact

  return (
    <Layout title="CONTACT">
      <main id="contact">
        <div className="vertical-center">
          <h1 className="page-title">Contact Me</h1>
          <div className="container">
            <ContactAside contactInfo={contactInfo} />
            <ContactSection />
          </div>
        </div>
      </main>
    </Layout>
  )
} 

const ContactAside = ({ contactInfo }) => (
  <aside className="contact-aside">
    <div>
      <h2>{contactInfo.title}</h2>
      <p>
        <em>{contactInfo.subtitle}</em>
      </p>
    </div>
    <div>
      {contactInfo.info.map(elem => (
        <div key={elem.title} className="info">
          <div>
            {verifyIcon(elem.iconName)}
            <em className="info-title">{elem.title}</em>
          </div>
          <p className="info-desc">{elem.desc}</p>
        </div>
      ))}
    </div>
  </aside>
)

const ContactSection = () => (
  <section className="contact-section">
    <form
      className="form"
      name="contact"
      method="post"
      action="https://getform.io/f/b5965691-22b2-431a-9634-ad26587abae2"
    >
      <h3 className="form-title">Fill out the form</h3>
      <div>
        <input
          className="form-contact"
          type="text"
          name="name"
          placeholder="Your name"
          required
        />
      </div>
      <div>
        <input
          className="form-contact"
          type="email"
          name="email"
          placeholder="Your email"
          required
        />
      </div>
      <div>
        <input
          className="form-contact"
          type="text"
          name="subject"
          placeholder="Subject"
          required
        />
      </div>
      <div>
        <textarea
          className="form-contact textarea"
          name="message"
          placeholder="Message"
          required
        ></textarea>
      </div>
      <div>
        <button className="form-contact button" type="submit">
          SEND
        </button>
      </div>
    </form>
  </section>
)

const verifyIcon = iconName => {
  switch (iconName) {
    case "Address":
      return <Address />
    case "Phone":
      return <Phone />
    case "Email":
      return <Email />
    default:
      console.log(iconName)
  }
}

export const contactQuery = graphql`
  query {
    site {
      siteMetadata {
        contact {
          title
          subtitle
          info {
            iconName
            title
            desc
          }
        }
      }
    }
  }
`

export default Contact
